/***************************************************************************/
/* 게시글 상세조회 전체 영역 */
.board-detail{
    width: 1000px;
    min-height: 700px;
    border:  1px solid #ccc;
    margin: 50px auto;
    padding: 20px;
}
  
  
/* 게시글 제목 */
.board-detail .board-title{
  margin : 0;
  padding: 20px 0;
  font-size: 1.8em;
  border-bottom : 3px solid #ccc;
}
  
  
/* 게시글 헤더 */
.board-header{
  padding: 20px 0;
  margin-bottom: 40px ;
  border-bottom : 3px solid #ccc;
  display: flex;
  justify-content: space-between;
}

/* 게시글 작성자 영역 */
.board-writer{
  display: flex;
  align-items: center;
}
  
/* 닉네임 */
.board-writer > span{
  font-weight: bold;
  margin-left : 1px;
}

/* 게시글 정보 */
.board-info{
  width: 320px;
  font-size: 1em;
}

.board-info > p{
  margin: 0;
}

.board-info span{
  width: 120px;
  display: inline-block;
  font-weight: bold;
}


/* 게시글 내용 */
.board-content{
  font-size: 18px;
  height: 400px;
}

/* 버튼 영역 */
.board-detail .board-btn-area{
  text-align: right;
}

/* 버튼 */
.board-detail .board-btn-area button{
  width: 80px;
  height: 30px;
  margin-left: 10px;
  font-weight: bold;
  border: 0;
  background-color: #455ba8;
  color : white;

  cursor: pointer;
}

.board-detail .board-btn-area button:hover{
  background-color: white;
  color : #455ba8;
  border: 2px solid #455ba8;
}